.btn {
  font-weight: 700;
  color: $color-white;
  font-family: $font-OpenSans;
  display: inline-block;
}

.opening__btn {
  background-color: $primary-blue;
  padding: 22px 92px 23px 92px;
  border-radius: 40px;
  box-shadow: 5px 10px 14px #07ACE629;
  display: inline-block;
  margin: 0 32px;
}

.pricing__btn {
  border-radius: 18px;
  position: relative;
  padding: 16px 57px;
}

.btn__basic {
  background-color: $color-red;
  top: 25px;
  left: 60px;

  @include notebook {
    left: 65px;
  }
}

.btn__professional {
  background-color: $primary-blue;
  box-shadow: 5px 10px 14px #07ACE629;
  top:25px;
  left: 80px;

  @include notebook {
    left: 75px;
    top: 45px;
  }
}

.btn__premium {
  background-color: $color-green;
  top: 25px;
  left: 60px;

  @include notebook {
    left: 65px;
  }
}